<template>
<div class="admin">
  <div class="body">
    <h1>
      {{msg}}
    </h1>
    <div class="btn-content" id="text-btn" v-on:click="logout">
      로그아웃
    </div>
    <div class="btn-content">
      <b-button variant="primary" v-on:click="campaign()">캠패인</b-button>
      <b-button variant="primary" v-on:click="adminWin(false)">관리자 생성</b-button>
    </div>
    <b-list-group class="list">
      <b-list-group-item button v-for="system in systems" :key="system.id">
        <span class="front_list">
        아이디: {{ system.name }} , 비밀번호: {{ system.password }} , 캠패인 시작일: {{ dateChange(system.time) }}
      </span>
        <span class="back_list">
          <b-button v-on:click="adminModify(system.name)" variant="primary">비밀번호 수정</b-button>
            <b-button v-on:click="adminDelect(system.name)" variant="primary">삭제</b-button>
        </span>
      </b-list-group-item>
      <!-- <b-list-group-item button>I am a button</b-list-group-item>
      <b-list-group-item button disabled>Disabled button</b-list-group-item>
      <b-list-group-item button>This is a button too</b-list-group-item> -->
    </b-list-group>
  </div>
  <div class="back" v-if="flag">
    <div id="add_win">
      <b-form>
        <b-form-group id="inputGroup" label="관리자 생성" label-for="input1">
          <b-form-input ref='id' v-bind:readonly="isReadOnly" id="exampleInput3" class="front_input" type="text" v-model="form.id" required placeholder="아이디 입력(10자 이하)">
          </b-form-input>
          <b-form-input ref='password' id="exampleInput4" class="front_input" type="password" v-model="form.pw" required placeholder="비밀번호 입력">
          </b-form-input>
          <b-form-input ref='password2' id="exampleInput5" class="front_input" type="password" v-model="form.pw2" required placeholder="비밀번호 확인">
          </b-form-input>
        </b-form-group>
        <!-- <b-button type="submit" variant="primary">{{msg}}</b-button> -->
        <b-button v-on:click="addAdminCancel" variant="primary">취소</b-button>
        <b-button v-on:click="addAdmin(admin_state)" variant="primary">등록</b-button>
      </b-form>
    </div>
  </div>
</div>
</template>

<script>
import NetValues from '../NetValues'
import DateUtill from '../lib/DateUtill'
export default {
  name: "Admin",
  data() {
    return {
      msg: "관리자 리스트",
      flag: false,
      isReadOnly: false,
      systems: [],
      form: {
        id: '',
        pw: '',
        pw2: ''
      },
      admin_state: 0
    };
  },
  mounted() {
    // this.$http.get("/api/session").then(response => {
    //   console.log("res:", response.data.res);
    //   if (response.data.res == NetValues.REQ_LOGOUT) {
    //     this.$router.push("/");
    //   } else if (response.data.res == NetValues.REQ_OK) {
    //     if (response.data.level != 1) {
    //       this.$router.go(-1);
    //     } else {
    //       // this.getAdminList();
    //     }
    //   }
    // });
    this.getAdminList();
  },
  methods: {
    dateChange(d) {
      d = parseInt(d);
      // console.log('date:', d);
      // let date_utill = new DateUtill();
      // let date = date_utill.mysqlTimeStampToDate(d);
      // return date.getFullYear() + '년' + date.getMonth() + '월' + date.getDate() + '일 ' + date.getHours() + '시' + date.getMinutes() + '분';
      return this.$moment(d).format("YYYY-MM-DD HH:mm");
    },
    msgRouter(msg) {
      if (msg == NetValues.REQ_NO) {
        alert('최고 관리자만 접근 가능합니다.');
        this.$router.go(-1);
      } else if (msg == NetValues.REQ_LOGOUT) {
        this.$router.push('/');
      } else if (msg == NetValues.REQ_DB_ERRO) {
        alert('DB작업중 에러가 발생했습니다.');
      }
    },
    logout: function() {
      this.$http.get('/api/logout')
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == NetValues.REQ_OK) {
            this.$router.push('/');
          } else {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        })
    },
    addAdmin: function(_add_id = 0) {
      if (_add_id != NetValues.DELETE_DB) {
        if (this.$str_check.void_check(this.form.id)) {
          this.form.id = '';
          this.$refs.id.focus();
          alert('아이디를 입력해 주세요.');
          return;
        } else if (this.form.id.length < 4 || this.form.id.length >= 10) {
          this.form.id = '';
          this.$refs.id.focus();
          alert('아이디는 4자리 이상 10자리 이하로 입력해 주세요.');
          return;
        }

        if (this.$str_check.void_check(this.form.pw)) {
          this.form.pw = '';
          this.form.pw2 = '';
          this.$refs.password.focus();
          alert('비밀번호를 입력해 주세요.');
          return;
        } else if (this.form.pw.length < 4) {
          this.form.pw = '';
          this.form.pw2 = '';
          this.$refs.password.focus();
          alert('비밀번호는 4자리 이상 입력해 주세요.');
          return;
        }
        if (this.$str_check.void_check(this.form.pw2)) {
          this.form.pw2 = '';
          this.$refs.password2.focus();
          alert('확인 비밀번호를 입력해 주세요.');
          return;
        }
        if (this.form.pw != this.form.pw2) {
          this.form.pw = '';
          this.form.pw2 = '';
          this.$refs.password.focus();
          alert('확인 비밀번호가 다릅니다. 다시 입력해 주세요.');
          return;
        }
      }
      let data = {
        id: this.form.id,
        pw: this.form.pw,
        add_id: _add_id
      };
      this.$http.post('/api/addAdmin', data)
        .then((response) => {
          console.log('res:', response.data.res);
          this.addAdminCancel();
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');
            this.getAdminList();
          } else if (response.data.res == NetValues.REQ_NOT_IS_ID) {
            this.form.id = '';
            this.form.pw = '';
            this.form.pw2 = '';
            this.$refs.id.focus();
            alert('이미 있는 아이디입니다. 다시 입력해 주세요.');
          } else {
            this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    campaign() {
      this.$router.push('/Campaign');
    },
    adminWin(isReadOnly = false, name = '', state = 0) {
      this.admin_state = state;
      this.flag = true;
      this.form.id = name;
      this.form.pw = '';
      this.form.pw2 = '';
      this.isReadOnly = isReadOnly;
      let _this = this;
      setTimeout(function() {
        if (isReadOnly) {
          _this.$refs.password.focus();
        } else {
          _this.$refs.id.focus();
        }
      }, 50);
    },
    addAdminCancel() {
      this.flag = false;
    },
    setList(data) {
      this.systems = [];
      for (let i = 0; i < data.length; ++i) {
        this.systems[i] = data[i];
      }
    },
    adminModify(name) {
      this.admin_state = NetValues.UPDATE_DB;
      this.adminWin(true, name, NetValues.UPDATE_DB);
    },
    adminDelect(name) {
      if (confirm('이벤트 캠패인이 관리자와 연결되어 있습니다. 되도록이면 비밀번호 변경으로 유지하시길 바랍니다. 그래도 정말 삭제를 원하시면 확인을 선택해 주세요.')) {
        this.admin_state = NetValues.DELETE_DB;
        this.form.id = name;
        this.addAdmin(this.admin_state);
      }
    },
    getAdminList(_front = 0, _end = 10) {
      let data = {
        front: _front,
        end: _end
      };
      this.$http.post('/api/getAdminList', data)
        .then((response) => {
          console.log('qery');
          console.log('qery:', response.data.qery);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');REQ_DB_ERRO
            this.setList(response.data.qery);
          } else {
            this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  margin-bottom: 30px;
  width: 100%;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 10px;
}

a {
  color: #42b983;
}
.admin{

}
.body {
  position: relative;
  max-width: 1280px;
  left: 50%;
  transform: translate(-50%);
}

.btn-content {
  position: relative;
  margin: 10px;
  text-align: right;
}

.list {
  position: relative;
  margin: 10px;
}

#text-btn {
  cursor: pointer;

}

#add_win {
  position: absolute;
  max-width: 700px;
  left: 50%;
  top: 30%;
  background-color: #ffffff;
  transform: translate(-50%);
  border: steelblue 3px solid;
  padding: 20px;
  z-index: 5000;
}

.front_input {
  margin-top: 10px;
}

.front_list {
  position: absolute;
  display: inline-block;
  text-align: left;
  /* font-weight: bold; */
  font-size: 1.1rem;
  line-height: 2.3rem;
}

.back_list {
  position: relative;
  display: inline-block;
  text-align: right;
  width: 100%;
}

.back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.5);
}
</style>



