<template>
<div class="campaign">
  <div class="body">
    <h1>
      {{msg}}
    </h1>
    <div class="btn-content" id="text-btn" v-on:click="logout">
      로그아웃
    </div>
    <b-list-group class="list">
      <b-list-group-item button v-for="system in systems" :key="system.id" class="list_v">
        <b-img @load="loaded" v-on:click="pupupWin(true, system.image_name)" class="img_btn" v-bind:src="imageDir(system.image_name)" width="30px" height="30px" /> 주최자: {{ system.client_name }} , 캠패인명: {{ system.title }} , 캠패인 시작일시: {{ dateChange(system.time) }} , 참여자 수: {{system.participant }}

        <b-button v-on:click="moveEvent(system.id)" variant="primary">이벤트 등록</b-button>
        <b-button v-on:click="campaignModify(system.id, system.client_name, system.title, system.time, system.participant)" variant="primary">수정</b-button>
        <b-button v-on:click="campaignDelect(system.id, system.image_name)" variant="primary">삭제</b-button>

      </b-list-group-item>
    </b-list-group>
    <div class="btn-content">
      <b-button v-on:click="addWin(true)" variant="primary">등록</b-button>
      <b-button variant="primary" v-on:click="admin" v-show="flag">관리자 리스트 이동</b-button>
    </div>
  </div>
  <div class="back" v-show="flag3">
    <b-img v-on:click="pupupWin(false)" class="center_image" v-bind:src="pupup_image_url" />
  </div>
  <div class="back" v-if="flag2">
    <div id="add_win">
      <b-form ref='root_form"' method='POST' enctype='multipart/form-data' name="fileUploadForm" v-on:submit.prevent="onSubmit">
        <b-form-group id="inputGroup" label="캠패인 생성" label-for="input1">
          주최자
          <b-form-input ref='client_name' id="client_name" class="front_input" type="text" v-model="form.client_name" required placeholder="주최자 입력(50자 이하)">
          </b-form-input>
          제목
          <b-form-input ref='title' id="title" class="front_input" type="text" v-model="form.title" required placeholder="제목 입력(50자 이하)">
          </b-form-input>
          참여자 수
          <b-form-input ref='participant' id="participant" class="front_input" type="number" v-model="form.participant" required placeholder="참여자 수">
          </b-form-input>시작 일시
          <div class="front_input2">
            <b-form-select @change.native="changeYear" class="select_input" v-model="selected1" :options="options1" />년
            <b-form-select @change.native="changeMonth" class="select_input2" v-model="selected2" :options="options2" />월
            <b-form-select class="select_input2" v-model="selected3" :options="options3" />일
            <b-form-select class="select_input2" v-model="selected4" :options="options4" />시
            <b-form-select class="select_input2" v-model="selected5" :options="options5" />분
          </div>
          <div class="front_input2">타이틀 이미지
            <b-form-file ref='fileinput' accept=".jpg, .png, .jpeg" name="log_image" v-model="file" :state="Boolean(file)" @change="onFileChange" placeholder="이미지(드래그 앤 드롭 가능)" drop-placeholder="Drop file here..." />
          </div>
          <b-button v-on:click="addWin(false)" variant="primary">취소</b-button>
          <b-button type="submit" variant="primary">등록</b-button>
        </b-form-group>
      </b-form>
    </div>
  </div>
</div>
</template>

<script>
import NetValues from '../NetValues'
import DateUtill from '../lib/DateUtill'
import MyDate from '../javascript_library/MyDate'
let _this = this;
export default {
  name: "Campaign",
  data() {
    return {
      msg: "캠패인 리스트",
      selected1: 2019,
      options1: [],
      selected2: 1,
      options2: [],
      selected3: 1,
      options3: [],
      selected4: 2,
      options4: [],
      selected5: 2,
      options5: [],
      flag: false,
      flag2: false,
      flag3: false,
      pupup_image_url: '',
      systems: [],
      file: null,
      form: {
        id: '',
        client_name: '',
        title: '',
        participant: 100
      },
      admin_state: 0
    };
  },
  mounted() {
    // this.$http.get("/api/session").then(response => {
    //   console.log("res:", response.data.res);
    //   if (response.data.res == NetValues.REQ_LOGOUT) {
    //     this.$router.push("/");
    //   } else if (response.data.res == NetValues.REQ_OK) {
    //     if (response.data.level == 1) {
    //       this.flag = true;
    //     } else {
    //       this.flag = false;
    //     }
    //   }
    // });
    this.getCampaignList();
  },
  methods: {
    imageDir(name) {
      return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/campaign_images/" + name;
    },
    pupupWin(v, name = '') {
      this.flag3 = v;
      if (name != '') {
        this.pupup_image_url = this.imageDir(name);
      }
    },
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      console.log('flag', this.flag);

      if (!files.length) {
        alert('파일을 선택해 주세요.');
        return
      }
      let names = files[0].name.split(".");
      let ext = names[names.length - 1];
      if (ext != 'jpeg' && ext != 'png'&& ext != 'jpg') {
        alert('png, jpeg, jpg 이미지 파일만 선택해 주세요.');
        this.$refs.fileinput.reset();
        return
      }
      if ((1 * 1024 * 1024) < files[0].size) {
        alert('이미지 파일은 1M 이하로 해야합니다.');
        this.$refs.fileinput.reset();
        return
      }
      var reader = new FileReader();
      let _this = this;
      reader.readAsDataURL(files[0]);
      reader.onload = function(read_e) {
        let img = new Image();
        console.log('reader.result:', read_e.target.result);
        img.onload = function(img_e) {
          console.log('target.width:', img_e.target.width);
          if (img_e.target.width > 500 || img_e.target.height > 500) {
            alert('이미지 가로 세로 사이즈는 500px 이하로 해야합니다.');
            // _this.file = null;
            _this.$refs.fileinput.reset();
            // _this.file_v = null;
          }
        }
        img.src = read_e.target.result;
      }
    },
    loaded(e) {

    },
    moveEvent(id) {
      this.$router.push({
        name: 'EventPage',
        params: {
          campaign_id: id
        }
      });
    },
    dateChange(d) {
      d = parseInt(d);
      return this.$moment(d).format("YYYY-MM-DD HH:mm");
    },
    changeYear(event) {
      console.log('this.selected1:', event.target.value);
      this.selected2 = 1;
      let my_date = new MyDate();
      let day = my_date.getNowTotalDay(event.target.value, this.selected2 - 1);
      this.options3 = [];
      for (let i = 0; i < day; ++i) {
        this.options3[i] = i + 1;
      }
      this.selected3 = 1;
    },
    changeMonth(event) {
      let my_date = new MyDate();
      let day = my_date.getNowTotalDay(this.selected1, event.target.value - 1);
      this.options3 = [];
      for (let i = 0; i < day; ++i) {
        this.options3[i] = i + 1;
      }
      this.selected3 = 1;
    },
    initDate() {
      for (let i = 0; i < 24; ++i) {
        this['options' + (i + 1)] = [];
      }
      let year = (this.$moment().format("YYYY") * 1);
      this.options1[0] = year;
      this.options1[1] = year + 1;
      this.selected1 = this.options1[0];

      for (let i = 0; i < 12; ++i) {
        this.options2[i] = i + 1;
      }
      this.selected2 = (this.$moment().format("MM") * 1);
      let my_date = new MyDate();
      let day = my_date.getNowTotalDay(year, this.selected2 - 1);
      for (let i = 0; i < day; ++i) {
        this.options3[i] = i + 1;
      }
      this.selected3 = (this.$moment().format("DD") * 1);

      for (let i = 0; i < 24; ++i) {
        this.options4[i] = i;
      }
      this.selected4 = 23;
      for (let i = 0; i < 60; ++i) {
        this.options5[i] = i;
      }
      this.selected5 = 0;
    },
    logout: function() {
      this.$http.get('/api/logout')
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == 1) {
            this.$router.push('/');
          } else {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    admin: function() {
      this.$http.get('/api/admin')
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == NetValues.REQ_OK) {
            this.$router.push('/Admin');
          } else if (response.data.res == NetValues.REQ_NO) {
            alert('최고 관리자만 접근 가능합니다.');
          } else if (response.data.res == NetValues.REQ_LOGOUT) {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    addWin(v, _add_state = 0) {
      this.admin_state = _add_state;
      this.initDate();
      this.form.client_name = '';
      this.form.title = '';
      this.form.participant = 100;

      this.file = null;
      this.flag2 = v;
      if (v) {
        let _this = this;
        setTimeout(function() {
          _this.$refs.client_name.focus();
        }, 50);
      }
    },
    campaignModify(_id, _client_name, _title, _time, _participant) {
      this.addWin(true, NetValues.UPDATE_DB);
      this.form.id = _id;
      this.form.client_name = _client_name;
      this.form.title = _title;
      this.form.participant = _participant;
      _time = parseInt(_time);

      let y = parseInt(this.$moment(_time).format("YYYY"));
      this.selected1 = y;
      let m = parseInt(this.$moment(_time).format("MM"));
      this.selected2 = m;

      let my_date = new MyDate();
      let day = my_date.getNowTotalDay(this.selected1, this.selected2 - 1);
      this.options3 = [];
      for (let i = 0; i < day; ++i) {
        this.options3[i] = i + 1;
      }
      let d = parseInt(this.$moment(_time).format("DD"));
      this.selected3 = d;
      let h = parseInt(this.$moment(_time).format("HH"));
      this.selected4 = h;
      let mm = parseInt(this.$moment(_time).format("mm"));
      this.selected5 = mm;
    },
    campaignDelect(_id, _image_name) {
      this.admin_state = NetValues.DELETE_DB;
      if (confirm('캠패인을 정말 삭제하시겠습니까?')) {
        let data = {
          add_id: this.admin_state,
          id: _id,
          image_name: _image_name
        };
        this.$http.post('/api/addCampaign', data)
          .then((response) => {
            console.log('res:', response.data.res);
            if (response.data.res == NetValues.REQ_OK) {
              // this.$router.push('/Admin');
              this.getCampaignList();
            } else if (response.data.res == NetValues.REQ_NO) {
              // alert('최고 관리자만 접근 가능합니다.');
            } else if (response.data.res == NetValues.REQ_LOGOUT) {
              this.$router.push('/');
            }
          })
          .catch(function(error) {
            console.log('error', error);
          });
      }
    },
    getCampaignList(_front = 0, _end = 10) {
      let data = {
        front: _front,
        end: _end
      };
      this.$http.post('/api/getCampaignList', data)
        .then((response) => {
          console.log('qery');
          console.log('qery:', response.data.qery);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');REQ_DB_ERRO
            if (response.data.level == 1) {
              this.flag = true;
            } else {
              this.flag = false;
            }
            this.setList(response.data.qery);
          } else {
            this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    setList(data) {
      this.systems = [];
      for (let i = 0; i < data.length; ++i) {
        this.systems[i] = data[i];
      }
    },
    msgRouter(msg) {
      if (msg == NetValues.REQ_NO) {
        // alert('최고 관리자만 접근 가능합니다.');
        alert('DB작업중 에러가 발생했습니다.');
        this.$router.go(-1);
      } else if (msg == NetValues.REQ_LOGOUT) {
        this.$router.push('/');
      } else if (msg == NetValues.REQ_DB_ERRO) {
        alert('DB작업중 에러가 발생했습니다.');
      }
    },
    onSubmit(e) {
      if (this.$str_check.void_check(this.form.client_name)) {
        this.form.client_name = '';
        this.$refs.client_name.focus();
        alert('주최자를 입력해 주세요.');
        return;
      } else if (this.form.client_name.length < 4 || this.form.client_name.length >= 50) {
        this.form.client_name = '';
        this.$refs.client_name.focus();
        alert('주최자는 4자리 이상 50자리 이하로 입력해 주세요.');
        return;
      }
      this.form.client_name = this.form.client_name.trim();
      if (this.$str_check.void_check(this.form.title)) {
        this.form.title = '';
        this.$refs.title.focus();
        alert('제목을 입력해 주세요.');
        return;
      } else if (this.form.title.length < 4 || this.form.title.length >= 50) {
        this.form.title = '';
        this.$refs.title.focus();
        alert('제목은 4자리 이상 50자리 이하로 입력해 주세요.');
        return;
      }
      this.form.title = this.form.title.trim();
      if (this.admin_state == NetValues.ADD_DB) {
        if (this.file == null) {
          alert('이미지 파일을 선택해 주세요.');
          // this.$refs.file.focus();
          return;
        }
      }
      if (parseInt(this.form.participant) < 1) {
        alert('참여자수는 1명 이상 입력해 주세요.');
        this.form.participant = 100;
        this.$refs.participant.focus();
        return;
      }
      let form = document.forms.namedItem('fileUploadForm');
      let data = new FormData(form);
      if (this.admin_state == NetValues.UPDATE_DB) {
        data.append('id', this.form.id);
      }
      data.append('add_id', this.admin_state);
      data.append('client_name', this.form.client_name);
      data.append('title', this.form.title);
      data.append('participant', this.form.participant);
      let date_utill = new DateUtill();
      let date_v = this.$moment(date_utill.converterToDate(this.selected1, this.selected2, this.selected3, this.selected4, this.selected5)).valueOf();
      data.append('time', date_v);
      // console.log('b-form-file:', this.file);
      // console.log('date_v:', date_v);
      // console.log('date_utill.converterToDate:', date_utill.converterToDate(this.selected1, this.selected2, this.selected3, this.selected4, this.selected5));
      // console.log('this.$moment(date_v).format("YYYY-MM-DD HH:mm:ss"):', this.$moment(date_v).format("YYYY-MM-DD HH:mm:ss"));
      // this.file = null;
      this.$http.post('/api/addCampaign', data)
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');
            this.addWin(false);
            this.getCampaignList();
          } else if (response.data.res == NetValues.REQ_NO) {
            // alert('최고 관리자만 접근 가능합니다.');
          } else if (response.data.res == NetValues.REQ_LOGOUT) {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  margin-bottom: 30px;
  width: 100%;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 10px;
}

a {
  color: #42b983;
}

.campaign {}
.list_v{
  text-align: left;
}
.body {
  position: relative;
  max-width: 1280px;
  left: 50%;
  transform: translate(-50%);
}

.center_image {
  position: relative;
  top: 50%;
  transform: translate(0%, -50%);
}

.img_btn {
  cursor: pointer;
}

.front_list {
  position: absolute;
  display: inline-block;
  text-align: left;
  /* font-weight: bold; */
  font-size: 1.1rem;
  line-height: 2.3rem;
}

.back_list {
  position: relative;
  display: inline-block;
  text-align: right;
  width: 100%;
}

.btn-content {
  position: relative;
  margin: 10px;
  text-align: right;
}

.list {
  position: relative;
  margin: 10px;
}

#text-btn {
  cursor: pointer;
}

#add_win {
  position: absolute;
  max-width: 700px;
  left: 50%;
  top: 30%;
  background-color: #ffffff;
  transform: translate(-50%);
  border: steelblue 3px solid;
  padding: 20px;
  z-index: 5000;
}

.front_input {
  position: relative;
  width: 500px;
  margin: 5px;
}

.front_input2 {
  width: 500px;
  margin-bottom: 20px;
}

.select_input {

  position: relative;
  display: inline;
  width: 100px;
}

.select_input2 {

  position: relative;
  display: inline;
  width: 70px;
}

.back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.5);
}
</style>



