<template>
<div class="event">
  <div class="body">
    <h1>
      이벤트 리스트
    </h1>
    <h3>
        {{msg}}
      </h3>
    <div class="btn-content" id="text-btn" v-on:click="logout">
      로그아웃
    </div>
    <b-list-group class="list">
      <b-list-group-item button v-for="system in systems" :key="system.id" class="list_v">
        <img v-on:click="pupupWin(true, system.image_name)" class="img_btn" v-bind:src="imageDir(system.image_name)" width="30px" height="30px" /> 상품명: {{ system.event_name }} , 봉: {{ system.bong }} , 승리자: {{ system.winner }} , 대기: {{system.start_time
        }}초, 유튜브대기: {{system.youtube_time }}초, 유튜브: {{ onOff(system.youtube_url) }}, 이메일내용: {{ onOff(system.email_content)}}

        <input type="radio" :id="system.id" :value="1" v-model="system.root_view" v-on:click="selectRadio(system.id)">선택
        <b-button v-on:click="eventModify(system.id, system.event_name, system.bong, system.winner, system.start_time, system.youtube_url, system.youtube_time, system.email_content)" variant="primary">수정</b-button>
        <b-button v-on:click="eventDelect(system.id, system.image_name)" variant="primary">삭제</b-button>

      </b-list-group-item>
      <!-- <b-list-group-item button>I am a button</b-list-group-item>
      <b-list-group-item button disabled>Disabled button</b-list-group-item>
      <b-list-group-item button>This is a button too</b-list-group-item> -->
    </b-list-group>
    <div class="btn-content">
      <b-button v-on:click="addWin(true)" variant="primary">등록</b-button>
      <b-button v-on:click="moveEvent" variant="primary">캠패인</b-button>
      <b-button variant="primary" v-on:click="admin" v-show="flag">관리자 리스트 이동</b-button>
    </div>
  </div>
  <div class="back" v-show="flag3">
    <img v-on:click="pupupWin(false)" class="center_image" v-bind:src="pupup_image_url" width="500px" height="500px" />
  </div>
  <div class="back" v-if="flag2">
    <div id="add_win">
      <b-form method='POST' enctype='multipart/form-data' name="fileUploadForm" v-on:submit.prevent="onSubmit">
        <b-form-group id="inputGroup" label="이벤트 생성" label-for="input1">
          상품명
          <b-form-input ref='event_name' id="event_name" class="front_input" type="text" v-model="form.event_name" required placeholder="상품명 입력(50자 이하)">
          </b-form-input>
          봉 (배수)
          <b-form-input ref='bong' id="bong" class="front_input" type="number" v-model="form.bong" required placeholder="봉 (배수)">
          </b-form-input>
          승리자 수
          <b-form-input ref='winner' id="winner" class="front_input" type="number" v-model="form.winner" required placeholder="승리자 수">
          </b-form-input>
          대기 초
          <b-form-input ref='start_time' id="start_time" class="front_input" type="number" v-model="form.start_time" required placeholder="대기 초">
          </b-form-input>
          유튜브 주소
          <b-form-input ref='youtube_url' id="youtube_url" class="front_input" type="text" v-model="form.youtube_url" placeholder="유튜브 주소(50자 이하)">
          </b-form-input>
          유튜브 상영 초
          <b-form-input ref='youtube_time' id="youtube_time" class="front_input" type="number" v-model="form.youtube_time" required placeholder="유튜브 상영 초">
          </b-form-input>
          이메일 내용
          <b-form-textarea id="email_content" v-model="form.email_content" placeholder="이메일 내용" rows="3" max-rows="6" />
        </b-form-group>
        <div class="front_input2">상품 이미지
          <b-form-file ref='fileinput' accept=".jpg, .png, .jpeg" name="log_image" v-model="file" :state="Boolean(file)" @change="onFileChange" placeholder="이미지(드래그 앤 드롭 가능)" drop-placeholder="Drop file here..." />
        </div>
        <!-- <b-button type="submit" variant="primary">{{msg}}</b-button> -->
        <b-button v-on:click="addWin(false)" variant="primary">취소</b-button>
        <b-button type="submit" variant="primary">등록</b-button>
      </b-form>
    </div>
  </div>
</div>
</template>

<script>
import NetValues from '../NetValues'
import DateUtill from '../lib/DateUtill'
import MyDate from '../javascript_library/MyDate'
export default {
  name: "Event",
  data() {
    return {
      msg: "",
      flag: false,
      flag2: false,
      flag3: false,
      pupup_image_url: '',
      systems: [],
      file: null,
      form: {
        id: '',
        event_name: '',
        bong: 1,
        winner: 5,
        start_time: 3,
        youtube_url: '',
        youtube_time: 3,
        email_content: ''
      },
      admin_state: 0
    };
  },
  mounted() {
    // this.$http.get("/api/session").then(response => {
    //   console.log("res:", response.data.res);
    //   if (response.data.res == NetValues.REQ_LOGOUT) {
    //     this.$router.push("/");
    //   } else if (response.data.res == NetValues.REQ_OK) {
    //     if (response.data.level == 1) {
    //       this.flag = true;
    //     } else {
    //       this.flag = false;
    //     }
    //   }
    // });
    this.getEventList();
  },
  methods: {
    imageDir(name) {
      return "https://s3.ap-northeast-2.amazonaws.com/minuapp.com.rsp/event_images/" + name;
    },
    pupupWin(v, name = '') {
      this.flag3 = v;
      if (name != '') {
        this.pupup_image_url = this.imageDir(name);
      }
    },
    onOff(s) {
      if (s == '') {
        return 'off';
      } else {
        return 'on'
      }
    },
    selectRadio(_id) {
      let data = {
        id: _id,
        root_id: this.$route.params.campaign_id
      };
      console.log('selectRadio');
      this.$http.post('/api/eventSelect', data)
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');
            // this.getEventList();
            this.setList(response.data.qery);
          } else if (response.data.res == NetValues.REQ_NO) {
            // alert('최고 관리자만 접근 가능합니다.');
          } else if (response.data.res == NetValues.REQ_LOGOUT) {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    dateChange(d) {
      d = parseInt(d);
      return this.$moment(d).format("YYYY-MM-DD HH:mm");
    },
    logout: function() {
      this.$http.get('/api/logout')
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == 1) {
            this.$router.push('/');
          } else {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        })
    },
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      console.log('flag', this.flag);

      if (!files.length) {
        alert('파일을 선택해 주세요.');
        return
      }
      let names = files[0].name.split(".");
      let ext = names[names.length - 1];
      if (ext != 'jpeg' && ext != 'png'&& ext != 'jpg') {
        alert('png, jpeg, jpg 이미지 파일만 선택해 주세요.');
        this.$refs.fileinput.reset();
        return
      }
      if ((1 * 1024 * 1024) < files[0].size) {
        alert('이미지 파일은 1M 이하로 해야합니다.');
        this.$refs.fileinput.reset();
        return
      }
      var reader = new FileReader();
      let _this = this;
      reader.readAsDataURL(files[0]);
      reader.onload = function(read_e) {
        let img = new Image();
        console.log('reader.result:', read_e.target.result);
        img.onload = function(img_e) {
          console.log('target.width:', img_e.target.width);
          if (img_e.target.width > 500 || img_e.target.height > 500) {
            alert('이미지 가로 세로 사이즈는 500px 이하로 해야합니다.');
            // _this.file = null;
            _this.$refs.fileinput.reset();
            // _this.file_v = null;
          }
        }
        img.src = read_e.target.result;
      }
    },
    moveEvent() {
      this.$router.push('/Campaign');
    },
    admin: function() {
      this.$http.get('/api/admin')
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == NetValues.REQ_OK) {
            this.$router.push('/Admin');
          } else if (response.data.res == NetValues.REQ_NO) {
            alert('최고 관리자만 접근 가능합니다.');
          } else if (response.data.res == NetValues.REQ_LOGOUT) {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    addWin(v, _add_state = 0) {
      this.admin_state = _add_state;

      this.form.event_name = '';
      this.form.bong = 1;
      this.form.winner = 5;
      this.form.start_time = 3;
      this.form.youtube_url = '';
      this.form.youtube_time = 3;
      this.form.email_content = '';


      this.file = null;
      this.flag2 = v;
      if (v) {
        let _this = this;
        setTimeout(function() {
          _this.$refs.event_name.focus();
        }, 50);
      }
    },
    eventModify(_id, _event_name, _bong, _winner, _start_time, _youtube_url, _youtube_time, _email_content) {
      this.addWin(true, NetValues.UPDATE_DB);
      this.form.id = _id;
      this.form.event_name = _event_name;
      this.form.bong = _bong;
      this.form.winner = _winner;
      this.form.start_time = _start_time;
      this.form.youtube_url = _youtube_url;
      this.form.youtube_time = _youtube_time;
      this.form.email_content = _email_content;


    },
    eventDelect(_id, _image_name) {
      this.admin_state = NetValues.DELETE_DB;
      if (confirm('이벤트를 정말 삭제하시겠습니까?')) {
        let data = {
          add_id: this.admin_state,
          id: _id,
          image_name: _image_name
        };
        this.$http.post('/api/addEvent', data)
          .then((response) => {
            console.log('res:', response.data.res);
            if (response.data.res == NetValues.REQ_OK) {
              // this.$router.push('/Admin');
              this.setList(response.data.qery);
            } else if (response.data.res == NetValues.REQ_NO) {
              // alert('최고 관리자만 접근 가능합니다.');
            } else if (response.data.res == NetValues.REQ_LOGOUT) {
              this.$router.push('/');
            }
          })
          .catch(function(error) {
            console.log('error', error);
          });
      }
    },
    getEventList(_front = 0, _end = 10) {
      console.log('this.$route.params.id:', this.$route.params.campaign_id);
      let data = {
        root_id: this.$route.params.campaign_id,
        front: _front,
        end: _end
      };
      this.$http.post('/api/getEventList', data)
        .then((response) => {
          console.log('qery');
          console.log('qery:', response.data.qery);
          console.log('response.data.root_qery:', response.data.root_qery);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');REQ_DB_ERRO
            this.setList(response.data.qery);
            this.msg = response.data.root_qery.title;
            if (response.data.level == 1) {
              this.flag = true;
            } else {
              this.flag = false;
            }
          } else {
            this.msgRouter(response.data.res);
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    },
    setList(data) {
      this.systems = [];
      for (let i = 0; i < data.length; ++i) {
        this.systems[i] = data[i];
      }
    },
    msgRouter(msg) {
      if (msg == NetValues.REQ_NO) {
        // alert('최고 관리자만 접근 가능합니다.');
        alert('DB작업중 에러가 발생했습니다.');
        this.$router.go(-1);
      } else if (msg == NetValues.REQ_LOGOUT) {
        this.$router.push('/');
      } else if (msg == NetValues.REQ_DB_ERRO) {
        alert('DB작업중 에러가 발생했습니다.');
      }
    },
    onSubmit(e) {

      if (this.$str_check.void_check(this.form.event_name)) {
        this.form.event_name = '';
        this.$refs.event_name.focus();
        alert('상품명을 입력해 주세요.');
        return;
      } else if (this.form.event_name.length < 4 || this.form.event_name.length >= 50) {
        this.form.event_name = '';
        this.$refs.event_name.focus();
        alert('상품명은 4자리 이상 50자 이하로 입력해 주세요.');
        return;
      }
      this.form.event_name = this.form.event_name.trim();

      if (parseInt(this.form.bong) < 1) {
        alert('봉은 1 이상 입력해 주세요.');
        this.form.bong = 1;
        this.$refs.bong.focus();
        return;
      }

      if (parseInt(this.form.winner) < 1) {
        alert('승리자는 1 이상 입력해 주세요.');
        this.form.winner = 5;
        this.$refs.winner.focus();
        return;
      }

      if (parseInt(this.form.start_time) < 1) {
        alert('대기 시간은 1초 이상 입력해 주세요.');
        this.form.start_time = 3;
        this.$refs.start_time.focus();
        return;
      }

      if (this.$str_check.void_check(this.form.youtube_url)) {
        this.form.youtube_url = '';
      } else if (this.form.youtube_url.length < 4 || this.form.youtube_url.length >= 50) {
        this.form.youtube_url = '';
        this.$refs.youtube_url.focus();
        alert('유뷰브 주소는 4자리 이상 50자 이하로 입력해 주세요.');
        return;
      }
      this.form.youtube_url = this.form.youtube_url.trim();
      if (parseInt(this.form.youtube_time) < 1) {
        alert('유튜브 시간은 1초 이상 입력해 주세요.');
        this.form.youtube_time = 3;
        this.$refs.youtube_time.focus();
        return;
      }
      if (this.$str_check.void_check(this.form.email_content)) {
        this.form.email_content = '';
      } else if (this.form.email_content.length < 4 || this.form.email_content.length >= 100) {
        this.form.email_content = '';
        this.$refs.email_content.focus();
        alert('이메일 내용은 4자리 이상 100자 이하로 입력해 주세요.');
        return;
      }
      this.form.email_content = this.form.email_content.trim();
      if (this.admin_state == NetValues.ADD_DB) {
        if (this.file == null) {
          alert('이미지 파일을 선택해 주세요.');
          // this.$refs.file.focus();
          return;
        }
      }

      let form = document.forms.namedItem('fileUploadForm');
      let data = new FormData(form);
      if (this.admin_state == NetValues.UPDATE_DB) {
        data.append('id', this.form.id);
      }

      data.append('root_id', this.$route.params.campaign_id);
      data.append('add_id', this.admin_state);
      data.append('event_name', this.form.event_name);
      data.append('bong', this.form.bong);
      data.append('winner', this.form.winner);
      data.append('start_time', this.form.start_time);
      data.append('youtube_url', this.form.youtube_url);
      data.append('youtube_time', this.form.youtube_time);
      data.append('email_content', this.form.email_content);


      this.$http.post('/api/addEvent', data)
        .then((response) => {
          console.log('res:', response.data.res);
          if (response.data.res == NetValues.REQ_OK) {
            // this.$router.push('/Admin');
            this.addWin(false);
            this.getEventList();
          } else if (response.data.res == NetValues.REQ_NO) {
            // alert('최고 관리자만 접근 가능합니다.');
          } else if (response.data.res == NetValues.REQ_LOGOUT) {
            this.$router.push('/');
          }
        })
        .catch(function(error) {
          console.log('error', error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  margin-bottom: 30px;
  width: 100%;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 10px;
}

a {
  color: #42b983;
}

.cmpaign {}

  .list_v{
    text-align: left;
  }
.body {
  position: relative;
  max-width: 1280px;
  left: 50%;
  transform: translate(-50%);
}

.center_image {
  position: relative;
  top: 50%;
  transform: translate(0%, -50%);
}

.img_btn {
  cursor: pointer;
}

.front_input2 {
  width: 500px;
  margin-bottom: 20px;
}

.front_list {
  position: absolute;
  display: inline-block;
  text-align: left;
  /* font-weight: bold; */
  font-size: 1.1rem;
  line-height: 2.3rem;
}

.back_list {
  position: relative;
  display: inline-block;
  text-align: right;
  width: 100%;
}

.btn-content {
  position: relative;
  margin: 10px;
  text-align: right;
}

.list {
  position: relative;
  margin: 10px;
}

#text-btn {
  cursor: pointer;
}

#add_win {
  position: absolute;
  max-width: 700px;
  left: 50%;
  top: 10%;
  background-color: #ffffff;
  transform: translate(-50%);
  border: steelblue 3px solid;
  padding: 20px;
  z-index: 5000;
}

.front_input {
  position: relative;
  width: 500px;
  margin: 5px;
}


.select_input {

  position: relative;
  display: inline;
  width: 100px;
}

.select_input2 {

  position: relative;
  display: inline;
  width: 70px;
}

.back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.5);
}
</style>



